import { Component, ElementRef, Input, ViewChild } from '@angular/core';
import { Track } from '../../interfaces/tracks';
import { Logger } from '../../class/logger';
import { LoggerColors } from '../../interfaces/logger';
import { IconType } from '../icon/icon.component';
import { KitRtc } from '../../services/kitrtc.service';

@Component({
  selector: 'kitrtc-track',
  templateUrl: './tracks.component.html',
  styleUrls: ['./tracks.component.css']
})
export class TrackComponent {
  @ViewChild('stream', { static: false }) stream?: ElementRef<HTMLVideoElement | HTMLAudioElement>;
  @Input() track?: Track
  @Input() local: boolean

  logger: Logger = new Logger();
  iconType = IconType;
  autoPlayPolicySkip: boolean = true;


  isLoaded: boolean = false;
  onPlayed: boolean = false;

  constructor(
    private kitRtc: KitRtc,
  ) {
    this.local = true;
  }

  ngOnInit() {
    if(this.track) this.track.trackComponent = this;
  }



  ngAfterViewInit() {
    if (!this.stream) return;
    if (this.local) {
      this.stream.nativeElement.muted = true;
    } else {
      this.stream.nativeElement.muted = false;
    }

    this.stream.nativeElement.controls = false;
    this.stream.nativeElement.autoplay = true;


    if (this.stream.nativeElement instanceof HTMLVideoElement) {
      this.stream.nativeElement.playsInline = true;
    }

    if (this.track) {
      this.track.getAutoSource = this.stream.nativeElement;
      this.stream.nativeElement.srcObject = this.track.stream;
      this.autoPlay();
    }
  }




  loadeddata(): void {
    if (!this.stream) return;
    this.isLoaded = true;
  }

  onPlaying(): void {
    if (!this.stream) return;
    this.onPlayed = true;
  }



  /**
   * Auto play
   * @description Auto play media element
   * @returns {Promise<void>}
   */
  async autoPlay(): Promise<void> {
    if (!this.stream) return;
    const media = this.stream.nativeElement;
    try {
      if (this.local) {
        media.muted = true;
      } else {
        media.muted = false;
      }
      media.play().then(() => {
        this.autoPlayPolicySkip = true;
      }, async (error: any) => {
        if (error.code === 20) { return; }
        if (error.code !== 0) this.logger.log(LoggerColors.RED, { event: 'auto-play', error });
        this.autoPlayPolicySkip = false;
        media.muted = true;
        await media.play().then(() => {
        }).catch((error) => {
          if (error.code !== 0) this.logger.log(LoggerColors.RED, { event: 'auto-play', message: error.message });
        });
      });
    } catch (error) {
      this.logger.log(LoggerColors.RED, { event: 'auto-play', message: error });
    }
  }




  /**
   * Auto play for policy
   */
  autoPlayForPolicy(): void{
    if(this.autoPlayPolicySkip) return;
    this.autoPlay();
    this.kitRtc.autoPlayPolicy();
  }


}
