import { Injectable } from '@angular/core';
import { Socket, io } from "socket.io-client";
import { Participant } from '../class/participant';
import { Room } from '../class/room';
import { Subject } from 'rxjs';
import { MediaOptions, Track } from '../interfaces/tracks';
import { Logger } from '../class/logger';
import { LoggerColors } from '../interfaces/logger';


/**
 * KitRtc is the main service of the library.
 * It allows you to connect to a server and create a room.
 * @description This service is the main entry point for using the library. It provides methods for connecting to a server and creating rooms for real-time communication.
 */



@Injectable({
  providedIn: 'root'
})


export class KitRtc {

  //Connection
  private server: string = "http://localhost:3000";
  private socket?: Socket;


  /**
   * Debug mode
   * @description Debug mode
   * @default false
   */
  isDebug: boolean = false;

  /**
   * Logger
   * @description Logger
   */
  protected logger: Logger = new Logger();


  /**
   * Participants
   * @description List of participants
   */
  participants: Participant[];

  /**
   * Local participant
   * @description Local participant
   */
  localParticipant: Participant;


  /**
   * Room name
   * @description Room name for join
   */
  roomName: string;

  /**
   * Room instance
   * @description Room instance for manage room
   */
  room: Room;

  /**
   * Reconnect
   * @description Reconnect to room after disconnection
   */
  reconnect: boolean;

  /**
   * Is connected
   * @description Is connected to server
   */
  isConnected: boolean ;


  /**
   * Media options
   * @description Media options for local participant
   */
  mediaOptions: MediaOptions = {
    video: {
      maxBitrate: 2000
    },
    audio: {
      maxBitrate: 200
    }
  }








  /**
   * @event onParticipantConnected
   * @description Event emitted when a participant joins the room
   * @param participant Participant connected
   */
  onParticipantConnected: Subject<Participant>;

  /**
   * @event onParticipantDisconnected
   * @description Event emitted when a participant leaves the room
   * @param participant Participant disconnected
   */
  onParticipantDisconnected: Subject<Participant>;

  /**
   * @event onRemoteMedia
   * @description Event emitted when a remote media is added
   * @param participant Participant added media
   * @param track Track of event
   */
  onRemoteMedia: Subject<[Participant, Track]>;

  /**
   * @event onRemoveLocalMedia
   * @description Event emitted when a local media is removed
   * @param participant Participant local added media
   * @param track Track removed of event
   */
  onRemoveLocalMedia: Subject<[Participant, Track]>;

  /**
   * @event onRemoveRemoteMedia
   * @description Event emitted when a remote media is added
   * @param participant Participant remote added media
   * @param track Track remote of event
   */
  onRemoveRemoteMedia: Subject<[Participant, Track]>;


  /**
   * @event onDisconnect
   * @description Event emitted when a socket is disconnected
   * @param reason Reason of disconnection
   */
  onDisconnect: Subject<Socket.DisconnectReason>;


  /**
   * @event onMuted
   * @description Event emitted when a participant is muted (Only for local participants for now.)
   * @param participant Participant muted
   * @param track Track muted
   */
  onMuted: Subject<[Participant, Track]>;


  /**
   * @event onUnMuted
   * @description Event emitted when a participant is unmuted (Only for local participants for now.)
   * @param participant Participant unmuted
   * @param track Track unmuted
   */
  onUnMuted: Subject<[Participant, Track]>;






  constructor(
  ) {

    /**
     * Participants
     */
    this.participants = [];
    this.localParticipant = new Participant(this, "", true);



    /**
     * Events
     */
    this.onDisconnect = new Subject<Socket.DisconnectReason>();
    this.onParticipantConnected = new Subject<Participant>();
    this.onParticipantDisconnected = new Subject<Participant>();
    this.onRemoteMedia = new Subject<[Participant, Track]>();
    this.onRemoveLocalMedia = new Subject<[Participant, Track]>();
    this.onRemoveRemoteMedia = new Subject<[Participant, Track]>();
    this.onMuted = new Subject<[Participant, Track]>();
    this.onUnMuted = new Subject<[Participant, Track]>();




    /**
     * Room
     */
    this.roomName = "";
    this.reconnect = true;
    this.room = new Room(this);
    this.isConnected = false;








    /**
     * Set default bitrate
     */
    if (this.mediaOptions) {
      if (this.mediaOptions.video) {
        this.mediaOptions.video.maxBitrate = this.mediaOptions.video?.maxBitrate || this.mediaOptions.video.maxBitrate;
      }
      if (this.mediaOptions.audio) {
        this.mediaOptions.audio.maxBitrate = this.mediaOptions.audio?.maxBitrate || this.mediaOptions.audio.maxBitrate;
      }
    }

  }


  /**
   * Connect with a server
   * @description Connect with a server
   * @param server Server url
   * @returns Promise<boolean>
   */
  async connect(server: string): Promise<boolean> {
    if (!server) {
      this.logger.log(LoggerColors.RED, { event: "connect", message: "server is not defined"});
      return false;
    }

    //connect to server with socket
    this.server = server;
    try {
      this.socket = io(this.server, {
        transports: ["websocket"],
        path: "/",
      });

      this.localParticipant = new Participant(this, this.socket?.id || "", true);
      this.handlingEventsServer()

      return new Promise((resolve, reject) => {
        this.socket?.once("connect", () => {
          resolve(true);
        });
        this.socket?.once("connect_error", (error: Error) => {
          this.logger.log(LoggerColors.RED, { event: "connecterror", message: error });
          //clear socket once events
          resolve(false);
        });
      });

    } catch (error) {
      if (this.isDebug) console.log(`MediaKitApp: error connecting to server (${error})`);
      return false;
    }

  }







  /**
   * Handle events from server
   * @description Handle events from server
   */
  private handlingEventsServer() {

    this.socket?.on("connect", () => {
      this.logger.log(LoggerColors.GREEN, { event: "connect", message: "connected to server" });
      this.isConnected = true;

      /**
       * Reconnect to room
       */
      if(this.roomName && this.reconnect){
        this.logger.log(LoggerColors.BLUE, { event: "reconnect", message: "reconnecting to room" });
        this.room.join(this.roomName).then((response) => {
          if(response){
            this.logger.log(LoggerColors.GREEN, { event: "reconnect", message: "reconnected to room" });
          } else {
            this.logger.log(LoggerColors.RED, { event: "reconnect", message: "error reconnected to room" });
          }
        }).catch((error) => {
          this.logger.log(LoggerColors.RED, { event: "reconnect", message: error});
        });
      }

    });


    this.socket?.on("disconnect", (event) => {
      this.logger.log(LoggerColors.RED, { event: "disconnect", message: event });
      this.onDisconnect.next(event);
      this.isConnected = false;
    });


    this.socket?.on("participants", async (response: any) => {
      //create participants lists
      this.participants = response.map((p: any) => {
        const participant = new Participant(this, p, false, false);
        //Emit event on joined participant
        this.onParticipantConnected.next(participant);
        //add track to connection
        participant.negotiated();
        return participant;
      });
    });

    this.socket?.on("participant-join", async (id: string) => {
      if (this.participants.find((p) => p.id === id)) { return; }

      //create participant
      const participant = new Participant(this, id, false, true);
      this.participants.push(participant);

      //Emit event on joined participant
      this.onParticipantConnected.next(participant);

      //add track to connection (deprecated)
      //participant.startNegotiation();
    });

    this.socket?.on("participant-left", (id: string) => {
      const participant = this.participants.find((p) => p.id === id);
      if (participant) {
        participant.close()
        this.participants = this.participants.filter((p) => p.id !== id);
        this.onParticipantDisconnected.next(participant);
      }
    });




    /**
     * WebRTC events
     */
    this.socket?.on("candidate", (event: any, id: string) => {
      const participant = this.participants.find((p) => p.id === event.id);
      if (participant) {
        participant.handleCandidate(event);
      }
    });

    this.socket?.on("offer", (offer: any) => {
      const participant = this.participants.find((p) => p.id === offer.id);
      if (participant) {
        participant.handleOffer(offer);
      }
    });

    this.socket?.on("answer", (answer: any) => {
      const participant = this.participants.find((p) => p.id === answer.id);
      if (participant) {
        participant.handleAnswer(answer);
      }
    });



  }


  /**
   * Get socket instance
   * @returns Socket
   */
  getSocket(): Socket | undefined {
    return this.socket;
  }



  /**
   * Auto play all tracks
   * @description Manual auto play for all participants.
   */
  autoPlayPolicy(): void{
    for (const participant of this.participants) {
      for (const track of participant.tracks) {
        if(track.trackComponent) track.trackComponent.autoPlay();
      }
    }
  }
}
