import { Component, ElementRef, Input, ViewChild } from '@angular/core';
import { Participant } from '../../../class/participant';
import { IconType } from '../../icon/icon.component';
import { Track, TrackKind } from '../../../interfaces/tracks';

@Component({
  selector: 'kitrtc-participant',
  templateUrl: './participant.component.html',
  styleUrls: ['./participant.component.scss']
})

export class ParticipantComponent {
  @ViewChild('participantElement', { static: false }) participantElement?: ElementRef<HTMLDivElement>;

  @Input() participant?: Participant;
  iconType = IconType;

  constructor() { }

  maximized(){
    if (!this.participantElement?.nativeElement) return;

    // Obtener el elemento nativo
    const element = this.participantElement.nativeElement as HTMLDivElement & {
      requestFullscreen?: () => Promise<void>;
      webkitRequestFullscreen?: () => Promise<void>;
      msRequestFullscreen?: () => Promise<void>;
    };

    //Verifica si el elemento est en pantalla completa
    if (document.fullscreenElement) {
      document.exitFullscreen();
      return;
    }

    // Verificar si el mtodo requestFullscreen es compatible
    if (element.requestFullscreen) {
      element.requestFullscreen();
    } else if (element.webkitRequestFullscreen) { /* Safari */
      element.webkitRequestFullscreen();
    } else if (element.msRequestFullscreen) { /* IE11 */
      element.msRequestFullscreen();
    }
  }


  /**
   * Have video participant
   * @description Check if the participant have video
   * @returns {boolean}
   */
  haveVideo(): boolean {
    if (!this.participant) return false;
    const find = this.participant.tracks.find(track => track.kind === TrackKind.VIDEO);
    if (find) return true;
    return false;
  }


  trackById(index: number, track: Track): string {
    return track.id;
  }
}
