import { Component } from '@angular/core';
import { KitRtc } from '../../../services/kitrtc.service';

@Component({
  selector: 'kitrtc-local-participant',
  templateUrl: './local-participant.component.html',
  styleUrls: ['./local-participant.component.scss']
})

export class LocalParticipantComponent {
  _kitRtc: KitRtc;

  constructor(
    public kitRtc: KitRtc,
  ) {
    this._kitRtc = kitRtc;
  }

  get localParticipant(){
    return this._kitRtc.localParticipant;
  }
}
