import { Component } from '@angular/core';
import { KitRtc } from '../../services/kitrtc.service';
import { IconType } from '../icon/icon.component';
import { Participant } from '../../class/participant';
import { TrackKind } from '../../interfaces/tracks';

@Component({
  selector: 'kitrtc-participants',
  templateUrl: './participants.component.html',
  styleUrls: ['./participants.component.scss']
})
export class ParticipantsComponent {
    _kitRtc: KitRtc;
    iconType = IconType;

    constructor(
      public kitRtc: KitRtc,
    ) {
      this._kitRtc = kitRtc;
    }


    get participants(){
      return this._kitRtc.participants;
    }


    isMuted(participant: Participant): boolean{
      const getAudioTrack = participant.tracks.find(track => track.kind === TrackKind.AUDIO);
      if(getAudioTrack){
        return getAudioTrack.track.muted
      }
      return false;
    }

    trackById(index: number, item: Participant) {
      return item.id
    }

}
